import {SetApi, SetSaveApi} from 'utils/Api';
import { Upload, Card, Button, message, Form, Input, Space } from 'antd';
import { useEffect, useState } from 'react';
import { PlusOutlined, MinusCircleOutlined } from '@ant-design/icons';

const { TextArea } = Input;

const About = () => {
  const [shareImageUrl, setShareImageUrl] = useState(null);
  const [shareForm] = Form.useForm();
  const [centerForm] = Form.useForm();

  const [imageUrl, setImageUrl] = useState(null);
  const [avatarUrl, setAvatarUrl] = useState(null);
  const [aboutForm] = Form.useForm();

  useEffect(() => {
    SetApi('about').then((res) => {
      aboutForm.setFieldsValue({
        _id: res.data._id,
        ...res.data.sets,
      });
    });

    SetApi('share').then((res) => {
      shareForm.setFieldsValue({
        _id: res.data._id,
        ...res.data.sets,
      });
			setShareImageUrl(res.data.sets.shareImageUrl)
    });

    SetApi('center').then((res) => {
      centerForm.setFieldsValue({
        _id: res.data._id,
      })
			setImageUrl(res.data.sets.imageUrl)
			setAvatarUrl(res.data.sets.avatarUrl)
    });
  }, []);


  const onChange = (info, type) => {
    if (info.file.status === 'done') {
      message.success('文件上传成功');
			if(type === 'img'){
      setImageUrl(info.file.response);
			}else if(type === 'avatar'){
				setAvatarUrl(info.file.response);
			}else if(type === 'shareImg'){
        setShareImageUrl(info.file.response);
			}
    } else if (info.file.status === 'error') {
      message.error('文件上传失败');
    }
  };

  const onFinishAbout = (value) => {
    const { _id, name, detail } = value;
    SetSaveApi({
      _id: _id,
      type: 'about',
      sets: { name, detail },
    });
  };

  const onFinishShare = (value) => {
    const { _id, notes, rules } = value;
    SetSaveApi({
      _id: _id,
      type: 'share',
      sets: {shareImageUrl, notes, rules },
    });
  };

  const onFinishCenter = (value) => {
    SetSaveApi({
      _id: value._id,
      type: 'center',
      sets: { imageUrl, avatarUrl },
    });
  };

  return (
    <>
      <Card title="项目说明" >
        <Form
          labelCol={{ span: 4 }}
          form={aboutForm}
          name="control-hooks"
          onFinish={onFinishAbout}
        >
          <Form.Item name="_id" style={{ display: 'none' }}>
            <Input />
          </Form.Item>
          <Form.Item name="name" label="标题" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
          <Form.Item name="detail" label="详情">
            <TextArea rows={4} placeholder="输入公告内容" />
          </Form.Item>
          <Form.Item wrapperCol={{ span: 4, offset: 4 }}>
            <Button type="link" htmlType="submit">
              保存
            </Button>
          </Form.Item>
        </Form>
      </Card>

      <Card title="分享中心设置" style={{marginTop: '2rem'}}>
      <Form
        name="control-hooks"
        onFinish={onFinishShare}
				form={shareForm}
      >
          <Form.Item name="_id" style={{ display: 'none' }}>
            <Input />
          </Form.Item>
        <Form.Item label="页面横幅">
          <Upload
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}
            name="file"
            action={process.env.REACT_APP_UPLOAD_SERVER}
            headers={
              localStorage.getItem('__admin_token')
                ? { Authorization: `Bearer ${localStorage.__admin_token}` }
                : null
            }
            showUploadList={false}
            onChange={(info) => onChange(info, 'shareImg')}
          >
            {shareImageUrl ? (
              <img
                src={process.env.REACT_APP_SERVER + shareImageUrl}
                alt="avatar"
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: '6rem',
                    height: '6rem',
                  }}
              />
            ) : (
              <div
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: '12rem',
                    height: '12rem',
                  }}>
                <PlusOutlined />
              </div>
            )}
          </Upload>
        </Form.Item>

        <Form.List name="notes">
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, ...restField }) => (
                <Space
                  key={key}
                  style={{ display: 'flex', marginBottom: 8 }}
                  align="baseline"
                >
                  <Form.Item
                    {...restField}
                    label="分享说明"
                    name={[name, 'rule']}
                    rules={[{ required: true, message: '请输入分享说明' }]}
                  >
                    <Input placeholder="分享说明" />
                  </Form.Item>
                  <MinusCircleOutlined onClick={() => remove(name)} />
                </Space>
              ))}
              <Form.Item>
                <Button
                  type="dashed"
                  onClick={() => add()}
                  block
                  icon={<PlusOutlined />}
                >
                  添加说明
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>

        <Form.List name="rules">
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, ...restField }) => (
                <Space
                  key={key}
                  style={{ display: 'flex', marginBottom: 8 }}
                  align="baseline"
                >
                  <Form.Item
                    {...restField}
                    label="分享规则"
                    name={[name, 'rule']}
                    rules={[{ required: true, message: '请输入分享规则' }]}
                  >
                    <Input placeholder="分享规则" />
                  </Form.Item>
                  <MinusCircleOutlined onClick={() => remove(name)} />
                </Space>
              ))}
              <Form.Item>
                <Button
                  type="dashed"
                  onClick={() => add()}
                  block
                  icon={<PlusOutlined />}
                >
                  添加规则
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>
        <Form.Item wrapperCol={{ span: 4, offset: 10 }}>
          <Button type="link" htmlType="submit">
            保存
          </Button>
        </Form.Item>
      </Form>
      </Card>

      <Card title="个人中心设置" style={{marginTop: '2rem'}}>
        <Form labelCol={{ span: 4 }} name="control-hooks" form={centerForm} onFinish={onFinishCenter}>
          <Form.Item name="_id" style={{ display: 'none' }}>
            <Input />
          </Form.Item>
          <Form.Item label="个人中心横幅">
            <Upload
              className="flex justify-center items-center"
              name="file"
              action={process.env.REACT_APP_UPLOAD_SERVER}
              headers={
                localStorage.getItem('__admin_token')
                  ? { Authorization: `Bearer ${localStorage.__admin_token}` }
                  : null
              }
              showUploadList={false}
              onChange={(info) => onChange(info, 'img')}
            >
              {imageUrl ? (
                <img
                  src={process.env.REACT_APP_SERVER + imageUrl}
                  alt="avatar"
                  style={{
                    width: '24rem',
                    height: '12rem',
                  }}
                />
              ) : (
                <div className="flex justify-center items-center w-72 h-24 bg-red-400">
                  <PlusOutlined />
                </div>
              )}
            </Upload>
          </Form.Item>
          <Form.Item label="用户头像">
            <Upload
              className="flex justify-center items-center"
              name="file"
              action={process.env.REACT_APP_UPLOAD_SERVER}
              headers={
                localStorage.getItem('__admin_token')
                  ? { Authorization: `Bearer ${localStorage.__admin_token}` }
                  : null
              }
              showUploadList={false}
              onChange={(info) => onChange(info, 'avatar')}
            >
              {avatarUrl ? (
                <img
                  src={process.env.REACT_APP_SERVER + avatarUrl}
                  alt="avatar"
                  style={{
                    width: '12rem',
                    height: '12rem',
                  }}
                />
              ) : (
                <div className="flex justify-center items-center w-24 h-24 bg-red-400">
                  <PlusOutlined />
                </div>
              )}
            </Upload>
          </Form.Item>
          <Form.Item wrapperCol={{ span: 4, offset: 4 }}>
            <Button type="link" htmlType="submit">
              保存
            </Button>
          </Form.Item>
        </Form>
      </Card>

    </>
  );
};

export default About;
